import { Component, ViewChild } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-perfil-create',
  templateUrl: 'perfil-create.html',
})
export class PerfilCreatePage {
  @ViewChild('fileInput') fileInput;
  public form                  : FormGroup;
  username: string = "";
  public nombre                 : any;
  public unidad		     : any;
  public estado		     : any;
  public costo		     : any;
  private _ID                  : String;
  public vunidad  :any=[];
  idempresa:any;
  public idunidad2		  :any=[];
  public pageTitle           	: string;
  pactualizacion:boolean=false;


  isDisablednit :any;
  constructor(public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.pactualizacion=this.navParams.get("actualizacion")
     // Define form validation - VERY basic!
     this.form = this._FB.group({
        nombre     : ['', Validators.required],
        unidad     : ['', Validators.required],
        estado     : ['', Validators.required]
       
     });

     this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })

 

  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {
    //this.pactualizacion=this.navParams.get("actualizacion")
    if(this.navParams.get("record")==null)
    {
       
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
      
     this.authService.getReg('UNIDADES/UNIDADES/' + this.idempresa,'catalogos').then((result) => {
      this.vunidad=result;
    }, (err) => {  
    this.msj.showError3('Error',err.error);
  });

    }
    else {
      
       this._ID 				=	this.navParams.data.record._id;
       
     this.authService.getReg('UNIDADES/UNIDADES/' + this.idempresa,'catalogos').then((result) => {
      this.vunidad=result;
      
      this.unidad=	this.navParams.data.record.unidad.id._id +','+this.navParams.data.record.unidad.id.nombre;
      this.idunidad2=this.unidad.split(',');
    
    }, (err) => {  
    this.msj.showError3('Error',err.error);
  });

    this.nombre		=	this.navParams.data.record.nombre;
       this.estado		=	this.navParams.data.record.estado;
   
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  

  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }



     let   options       : any	     = { 
       idempresa:this.idempresa,
     nombre : this.form.controls['nombre'].value,
     unidad : {id:this.idunidad2[0],nombre:this.idunidad2[1]},  
     estado : this.form.controls['estado'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' perfil '}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'perfils').then((result) => {
              if (!this.form.valid) { return; }
              console.log(result)
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'perfils').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }

  
 

  onSelectChange(selectedValue: any) {
    this.idunidad2=selectedValue.split(',');
      }
  
  

}



